.auth-form {
  width: 40rem;
  max-width: 80%;
  margin: 5rem auto;
}

.form-control label,
.form-control input {
  width: 100%;
  display: block;
}

.form-control label {
  margin-bottom: 0.5rem;
}

.form-control {
  margin-bottom: 1rem;
}

.form-actions button {
  background: grey;
  font: inherit;
  border: 1px solid black;
  border-radius: 5px;
  padding: 0.25rem 1rem;
  margin: 1rem 0;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.26);
  color: white;
}

.form-actions button:hover,
.form-actions button:active {
  background: rgb(75, 75, 75);
  border-color: rgb(75, 75, 75);
}
